export function changeLanguage(lang) {
    i18n.changeLanguage(lang);
    setCookie("lang", lang);
}

export function initLanguage(lang) {
    i18n.init({
        resources,
        lng: lang
    });
}

export function getLanguage() {
    if(getCookie("lang") === undefined) {
        setCookie("lang", "en");
    }
    return getCookie("lang");
}